import { DynamicServiceClient, DynamicServiceClientParams } from '@kbase/ui-lib';

interface TaxonomyAPIParams extends DynamicServiceClientParams { }

type TaxonID = string;

interface GetAncestorsParams {
    id: TaxonID;
}

interface GetDescendentsParams {
    id: TaxonID;
}

